import React, { Component } from 'react';
import { Table, Button } from 'antd';
import { setStudentNotEligibleExam } from 'dump/data';

const columns = [
  {
    title: 'Thao tc',
    dataIndex: 'operation',
    render: () => (<Button type="primary">Thm</Button>)
  },
  {
    title: 'M sinh vin',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'H v tn',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Tn lp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Tn mn',
    dataIndex: 'courseName',
    key: 'courseName',
  },
  {
    title: 'L do',
    dataIndex: 'reasonNotEligibleExam',
    key: 'reasonNotEligibleExam',
  }
];

class TableStudentNotChecked extends Component {
  state = {};
  render() {
    return (
      <Table bordered dataSource={setStudentNotEligibleExam} columns={columns} />
    );
  }
}

export default TableStudentNotChecked;



